@font-face {
  font-family: "Lato";
  src: url('../fonts/Lato-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url('../fonts/Lato-Black.ttf') format('truetype');
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url('../fonts/Lato-Heavy.ttf') format('truetype');
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url('../fonts/Lato-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url('../fonts/Lato-Semibold.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.container {
  width: 360px;
  margin: 0 auto;
}
.main-header {
  background-color: #0a0a0a;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__link--logo {
  text-decoration: none;
  margin: 18px 20px;
}
.about {
  width: 360px;
  margin: 0 auto;
  background-image: url("../img/main-photo.png");
  min-height: 586px;
}
.about__wrapper {
  width: 230px;
  margin-left: 20px;
}
.about__title {
  color: #fcfbfb;
  font-size: 24px;
  font-weight: 400;
  line-height: 32.58px;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  padding-top: 153px;
  margin-bottom: 35px;
}
.about__type {
  color: #fcfbfb;
  font-size: 14px;
  font-weight: 400;
  line-height: 21.27px;
}
.about__description {
  color: #fcfbfb;
  /* Style for "Поиск и бр" */
  width: 155px;
  font-size: 14px;
}
.presentation {
  width: 360px;
  margin: 0 auto;
  background-color: #1c1b19;
}
.presentation__wrapper {
  width: 320px;
  margin: 0 auto;
}
.presentation__slogan {
  margin: 0;
  padding: 0;
  width: 156px;
  min-height: 196px;
  font-family: "Lato-Black";
  color: #000000;
  font-family: "Lato - Black";
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.64px;
  background-color: #ffb600;
}
.presentation__slogan--time {
  color: #000000;
  font-family: "Lato - Semibold";
  font-size: 12px;
  font-weight: 400;
  line-height: 15.05px;
  letter-spacing: 1px;
}
